@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap");
@font-face {
  font-family: LNR;
  src: url(/assets/Font/LNR.ttf);
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 192rem;
  margin: 0 auto;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Playfair Display", serif;
  font-size: 1.6rem;
  color: #000000;
  background-color: #ffffff;
}

.header {
  padding: 1.5rem 7.5rem;
  height: 100vh;
}

.hero {
  margin-top: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero__image {
  flex: 1;
  margin-right: 10rem;
}

.heading {
  flex: 1;
  justify-self: stretch;
}
.heading__text {
  font-family: "LNR", Arial;
  font-size: 5rem;
  font-weight: normal;
  color: #ff2e27;
}
.heading__line {
  background: #000000;
  width: 13rem;
  height: 0.3rem;
  margin-top: 3rem;
}
.heading__desc {
  font-family: "Playfair Display", serif;
  font-size: 2rem;
  margin-top: 2rem;
}
.heading__btn {
  display: inline-block;
  margin-top: 2rem;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn {
  display: flex;
}
.btn__link {
  flex: 0 0 17.5rem;
  padding: 1rem 2rem 1rem 2.5rem;
  border: 0.3rem solid #ff2e27;
  border-radius: 1.5rem;
  color: #ff2e27;
  font-size: 2rem;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn__text {
  font-family: "Playfair Display", serif;
}